.progress-bar {
  width: 100%;
  background-color: #FEFEFE;
  height: 25px;
  position: relative;
  overflow: hidden;
  box-shadow: inset 0 2px 6px rgba(35, 41, 46, 0.35);
  transition: all .25s; }
  [class*="theme-dark"] .progress-bar,
  [class*="dark-theme"] .progress-bar {
    background-color: #2c2c33; }
  .progress-bar > .bar {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: block;
    background-color: #6eb3e0;
    box-shadow: inset 0 2px 6px rgba(35, 41, 46, 0.35);
    transform: translateX(-100%);
    transition: transform .55s ease-in-out, background-color .35s .55s; }
  .progress-bar.thin {
    height: 10px;
    box-shadow: none; }
    .progress-bar.thin > .bar {
      box-shadow: none; }
  .progress-bar.no-shadow {
    box-shadow: none; }
    .progress-bar.no-shadow > .bar {
      box-shadow: none; }
  .progress-bar.gradient > .bar {
    background-image: linear-gradient(45deg, #6eb3e0 60%, #2475aa); }
  .progress-bar.striped > .bar {
    background-image: repeating-linear-gradient(45deg, #449dd7, #449dd7 0.75rem, transparent 0, transparent 1.5rem); }
  .progress-bar.striped.animated > .bar {
    background-size: 200% 200%;
    animation: striped_animation 12s linear infinite; }
  .progress-bar.rounded {
    border-radius: 6px; }
  .progress-bar.success > .bar {
    background-color: #6ac479; }
  .progress-bar.success.gradient > .bar {
    background-image: linear-gradient(45deg, #6ac479 60%, #317d3e); }
  .progress-bar.success.striped > .bar {
    background-image: repeating-linear-gradient(45deg, #47b459, #47b459 0.75rem, transparent 0, transparent 1.5rem); }
  .progress-bar.success.striped.animated > .bar {
    background-size: 200% 200%;
    animation: striped_animation 12s linear infinite; }
  .progress-bar.alert > .bar {
    background-color: #e6c469; }
  .progress-bar.alert.gradient > .bar {
    background-image: linear-gradient(45deg, #e6c469 60%, #b28a1e); }
  .progress-bar.alert.striped > .bar {
    background-image: repeating-linear-gradient(45deg, #dfb33d, #dfb33d 0.75rem, transparent 0, transparent 1.5rem); }
  .progress-bar.alert.striped.animated > .bar {
    background-size: 200% 200%;
    animation: striped_animation 12s linear infinite; }
  .progress-bar.error > .bar {
    background-color: #d15656; }
  .progress-bar.error.gradient > .bar {
    background-image: linear-gradient(45deg, #d15656 60%, #842424); }
  .progress-bar.error.striped > .bar {
    background-image: repeating-linear-gradient(45deg, #c03434, #c03434 0.75rem, transparent 0, transparent 1.5rem); }
  .progress-bar.error.striped.animated > .bar {
    background-size: 200% 200%;
    animation: striped_animation 12s linear infinite; }

@keyframes striped_animation {
  100% {
    background-position: 100% 100%; } }
